<template>
  <div class="sold-dispatch">
    <BackBtn pagenum="3"></BackBtn>
    <div class="sold-dispatch-pic">
      <div class="sold-dispatch-pic-title title1">房源照片（9图）</div>
      <div class="sold-dispatch-pic-content">
        <div
          class="sold-dispatch-pic-content-box"
          v-for="item in pageInfo.picData"
          v-bind:key="item.id"
        >
          <img src="../../public/imgs/plus.png" class="sold-dispatch-pic-content-box-img" />
          <input
            type="file"
            accept="image/*"
            id="file"
            class="photo-input"
            @change="addNewPhoto($event,item.id)"
          />
          <div class="photo-show-pic" :style="item.value"></div>
        </div>
      </div>
      <div class="sold-dispatch-pic-title title1">房源视频（15s以内）</div>
      <div class="sold-dispatch-pic-content">
        <div
          class="sold-dispatch-pic-content-box"
          v-for="item in pageInfo.videoData"
          v-bind:key="item.id"
        >
          <img src="../../public/imgs/plus.png" class="sold-dispatch-pic-content-box-img" />
          <input
            type="file"
            accept="video/*"
            id="file"
            class="photo-input"
            @change="addNewVideo($event,item.id)"
          />
          <video class="video-show-pic" :src="item.value"></video>
        </div>
      </div>
    </div>

    <component
      v-for="item in pageInfo.formList"
      v-bind:key="item.id"
      :is="item.type"
      :formdata="item"
      :style="item.exStyle"
    ></component>
    <div class="bottom-blank"></div>
    <div class="submit-btn" @click="submit">提交</div>
  </div>
</template>

<script>
import FormInput from '../components/FormInput';
import CheckboxParten1 from '../components/CheckboxParten1';
import RadioParten1 from '../components/RadioParten1';
import BackBtn from '../components/BackBtn';
export default {
    name: 'Page3DispatchSold',
    data: function() {
        return {
            soldData: {
                photoCount: 0,
                videoCount: 0,
            },
            pageInfo: {
                picData: [{ id: 0, value: '' }],
                videoData: [{ id: 0, value: '' }],
                formList: [
                    {
                        id: 0,
                        type: 'FormInput',
                        placeholder: '请输入房东姓名',
                        title: '房东姓名：',
                    },
                    {
                        id: 1,
                        type: 'FormInput',
                        placeholder: '请输入房东手机号',
                        title: '房东手机号',
                    },
                    {
                        id: 2,
                        type: 'FormInput',
                        placeholder: '请输入房屋标题',
                        title: '房屋标题',
                    },
                    {
                        id: 3,
                        type: 'FormInput',
                        placeholder: '请选择户型',
                        title: '户型',
                    },
                    {
                        id: 4,
                        type: 'FormInput',
                        placeholder: '请输入面积',
                        title: '面积',
                    },
                    {
                        id: 5,
                        type: 'FormInput',
                        placeholder: '选择租金类型，请输入租金',
                        title: '租金',
                    },
                    {
                        id: 6,
                        type: 'FormInput',
                        placeholder: '请输入楼层',
                        title: '楼层',
                    },
                    {
                        id: 7,
                        type: 'FormInput',
                        placeholder: '楼梯房/电梯房',
                        title: '楼房类型',
                    },
                    {
                        id: 8,
                        type: 'FormInput',
                        placeholder: '请选择朝向',
                        title: '朝向',
                    },
                    {
                        id: 9,
                        type: 'FormInput',
                        placeholder: '请选择起租日期',
                        title: '起租日期',
                    },
                    {
                        id: 10,
                        type: 'FormInput',
                        placeholder:
                            '水费，电费，燃气费，宽带费，物业费，取暖费，有线电视费，停车费，可多选',
                        title: '租金包含',
                    },
                    {
                        id: 11,
                        type: 'FormInput',
                        placeholder: '请选择看房时间',
                        title: '看房时间',
                    },
                    {
                        id: 12,
                        type: 'FormInput',
                        placeholder: '请输入宜住人数',
                        title: '宜住人数',
                    },
                    {
                        id: 13,
                        type: 'FormInput',
                        placeholder: '请选择房屋配置',
                        title: '房屋配置',
                    },
                    {
                        id: 14,
                        type: 'FormInput',
                        placeholder: '请输入房屋亮点',
                        title: '房屋亮点',
                    },
                    {
                        id: 15,
                        type: 'FormInput',
                        placeholder: '请输入出租要求',
                        title: '出租要求',
                    },
                    {
                        id: 16,
                        type: 'FormInput',
                        placeholder: '请输入房间描述',
                        title: '房间描述',
                    },
                    {
                        id: 17,
                        type: 'CheckboxParten1',
                        name: 'houseSpecial',
                        title1: '房源特色',
                        title2: '可多选',
                        list: [
                            { id: 0, name: '可短租', checked: false },
                            { id: 1, name: '可短租', checked: false },
                            { id: 2, name: '可短租', checked: false },
                            { id: 3, name: '可短租', checked: false },
                            { id: 4, name: '可短租', checked: false },
                            { id: 5, name: '可短租', checked: false },
                            { id: 6, name: '可短租', checked: false },
                        ],
                    },
                    {
                        id: 18,
                        type: 'FormInput',
                        placeholder: '选择推广套餐',
                        title: '推广套餐',
                        exStyle: { 'margin-top': '3vw' },
                    },
                    {
                        id: 19,
                        type: 'RadioParten1',
                        name: 'agree promise',
                        checked: false,
                        text: ['我承认不收客户中介费'],
                        fontClass: 'content2',
                        activeClass: 'content3',
                    },
                    {
                        id: 20,
                        type: 'RadioParten1',
                        name: 'deposit',
                        checked: false,
                        text: [
                            '保证金',
                            '支付保证金的房源，必须房源真实，如举报不真实，则不退保证金，反之签约成功',
                            '保证金可申请退回',
                            '如发布虚假房源则可能永久封号',
                        ],
                        fontClass: 'content3 ',
                        activeClass: 'content3',
                    },
                ],

                radioList: [
                    {
                        name: 'agree promise',
                        checked: false,
                        text: ['我承认不收客户中介费'],
                        fontClass: 'content2',
                        activeClass: 'content3',
                    },
                    {
                        name: 'deposit',
                        checked: false,
                        text: [
                            '保证金',
                            '支付保证金的房源，必须房源真实，如举报不真实，则不退保证金，反之签约成功',
                            '保证金可申请退回',
                            '如发布虚假房源则可能永久封号',
                        ],
                        fontClass: 'content3 ',
                        activeClass: 'content3',
                    },
                ],
            },
        };
    },
    created: function() {
        // this.addNewPhoto();
        // this.addNewVideo();
        this.$bus.on('checkbox click', this.checkboxHouseSpecial);
        this.$bus.on('radio click', this.radioChange);
    },
    components: {
        FormInput,
        CheckboxParten1,
        RadioParten1,
        BackBtn,
    },
    methods: {
        submit: function() {
            this.$bus.emit('page3 submit', {});
        },
        addNewPhoto: function(e, id) {
            console.log(id);
            console.log(e.target.files[0]);

            var that = this;
            var file = e.target.files[0];
            if (file.type.indexOf('image') == 0) {
                var reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onload = function(e) {
                    // 图片base64化
                    var newUrl = this.result;
                    console.log(newUrl);
                    that.pageInfo.picData[
                        that.pageInfo.picData.length - 1
                    ].value =
                        'background:url(' + newUrl + ');backgroundSize:cover';
                    var obj = { id: that.pageInfo.picData.length, value: '' };
                    that.pageInfo.picData.push(obj);
                };
            }

            return;
        },
        addNewVideo: function(e, id) {
            var that = this;
            var file = e.target.files[0];
            if (file.type.indexOf('video') == 0) {
                var reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onload = function(e) {
                    // 图片base64化
                    var newUrl = this.result;
                    console.log(newUrl);
                    that.pageInfo.videoData[
                        that.pageInfo.videoData.length - 1
                    ].value = newUrl;
                    var obj = { id: that.pageInfo.videoData.length, value: '' };
                    that.pageInfo.videoData.push(obj);
                };
            }
        },
        checkboxHouseSpecial: function(data) {
            if (data.item.name == 'houseSpecial') {
                var list = this.pageInfo.formList;
                list.forEach(element => {
                    if (element.id == data.item.id) {
                        console.log(element);
                        element.list.forEach(items => {
                            if (items.id == data.id) {
                                items.checked = !items.checked;
                            }
                        });
                    }
                });
                this.pageInfo.checkBoxData.list = list;
            }
        },
        radioChange: function(data) {
            console.log(data);
            this.pageInfo.formList.forEach(item => {
                if (item.id == data.id) {
                    item.checked = !item.checked;
                }
            });
        },
    },
};
</script>
<style scoped>
.submit-btn {
    position: fixed;
    bottom: 0;
}
</style>

<style>
.line {
    width: 100%;
    height: 3vw;
}
</style>


