<template>
  <div class="back-part" @click="returnBack"></div>
</template>

<script>
export default {
    name: 'BackBtn',
    methods: {
        returnBack: function() {
            this.$bus.emit('return', { page: this.pagenum });
        },
    },
    props: ['pagenum'],
};
</script>

<style scoped>
.back-part {
    width: 100%;
    height: 10vw;
    background: #fff;
    margin-bottom: 2vw;
}
</style>


