<template>
  <div id="app page">
    <Page1></Page1>
    <Page2SoldPart v-show="pageIndex==2"></Page2SoldPart>
    <Page3DispatchSold v-show="pageIndex==3"></Page3DispatchSold>
    <Page4SeekPart v-show="pageIndex==4"></Page4SeekPart>
    <!-- <TextBlock v-show="pageIndex==3"></TextBlock> -->
  </div>
</template>

<script>
import TextBlock from './components/TextBlock';
import Page1 from './components/Page1';
import Page2SoldPart from './components/Page2SoldPart';
import Page3DispatchSold from './components/Page3DispatchSold';
import Page4SeekPart from './components/Page4SeekPart';

export default {
    name: 'app',
    components: {
        Page1,
        Page2SoldPart,
        Page3DispatchSold,
        Page4SeekPart,
    },
    data: function() {
        return {
            pageIndex: 0,
            sharedState: window.store.state,
            backgroundInfo: {
                loadComplete: false, // 背景音乐素材是否加载完毕
                instance: null, // 背景音乐实例
            },
        };
    },
    computed: {},
    created: function() {
        this.$bus.on('page1 select', this.toPage2Or4);
        this.$bus.on('page2 submit', this.toPage3);

        this.$bus.on('return', this.returnPage);
    },

    methods: {
        toPage2Or4(data) {
            console.log(data, data.id == 0);
            if (data.id == 0) {
                this.pageIndex = 2;
            } else {
                this.pageIndex = 4;
            }
        },
        toPage3() {
            this.pageIndex = 3;
        },
        returnPage(data) {
            console.log(data);
            switch (data.page) {
                case '2':
                    this.pageIndex = 1;
                    break;
                case '3':
                    this.pageIndex = 2;
                    break;
                case '4':
                    this.pageIndex = 1;
                    break;
            }
        },
    },
};
</script>

<style lang="scss">
@import url('./style/common.scss');
body {
    --rem-height: 0rem;
    --px-height: 0px;
    --circle-radius: 0px;
}

#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
}

.common-bg {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    background-size: cover;
}

.page1-bg {
    @extend .common-bg;
    background-image: url('../public/imgs/1.jpg');
}

.page2-bg {
    @extend .common-bg;
    background-image: url('../public/imgs/2.jpg');
}

.page3-bg {
    @extend .common-bg;
    background-image: url('../public/imgs/3.jpg');
}

.page4-bg {
    @extend .common-bg;
    background-image: url('../public/imgs/4.jpg');
}

.page-hint {
    background-color: #ffffff;
    color: #000000;
    width: 75vw;
    height: 10vw;
    font-size: 15px;
    line-height: 10vw;
    text-align: center;
}

.page-show {
    z-index: 5;
    opacity: 1;
}

.page-animate {
    animation: clipCircleIn 0.5s linear forwards;
    z-index: 10;
    opacity: 1;
}

.page-hide {
    z-index: 0;
    opacity: 0;
}

@keyframes clipCircleIn {
    0% {
        clip-path: circle(0 at 0% 100%);
    }
    100% {
        clip-path: circle(var(--circle-radius) at 0% 100%);
    }
}
</style>
