<template>
  <div class="page seek-part">
    <BackBtn pagenum="4"></BackBtn>
    <div class="seek-type">
      <div class="seek-type-text title1">类型</div>
      <div class="seek-type-select">
        <div
          class="seek-type-radio"
          :class="{'seek-type-radio-active':radioChecked==item.id}"
          v-for="item in radioData"
          :key="item.id"
          @click="radioCheck(item.id)"
        >{{item.text}}</div>
      </div>
    </div>
    <component :is="seekMoney.type" :formdata="seekMoney"></component>
    <AddressSelector></AddressSelector>
    <TimeSelector></TimeSelector>
    <div class="seek-type">
      <div class="seek-type-text title1">室友要求</div>
      <div class="seek-type-select">
        <div
          class="seek-type-radio2"
          :class="{'seek-type-radio2-active':radio2Checked==item.id}"
          v-for="item in radio2Data"
          :key="item.id"
          @click="radio2Check(item.id)"
        >{{item.text}}</div>
      </div>
    </div>
    <div class="seek-textarea">
      <div class="seek-textarea-title title1">描述</div>
      <textarea class="seek-textarea-content"></textarea>
    </div>

    <div class="submit-btn" @click="submit">提交</div>
  </div>
</template>

<script>
import AddressSelector from '../components/AddressSelector';
import TimeSelector from '../components/TimeSelector';
import BackBtn from '../components/BackBtn';

import FormInput from '../components/FormInput';
export default {
    name: 'Page4SeekPart',
    data: function() {
        return {
            seekMoney: {
                id: 0,
                type: 'FormInput',
                placeholder: '请输入租金',
                title: '租金',
            },
            radioChecked: 0,
            radio2Checked: 0,
            radioData: [
                {
                    text: '整租',
                    id: 0,
                },
                {
                    text: '拼租',
                    id: 1,
                },
            ],
            radio2Data: [
                {
                    text: '男女不限',
                    id: 0,
                },
                {
                    text: '限男生',
                    id: 1,
                },
                {
                    text: '限女生',
                    id: 2,
                },
            ],
        };
    },
    components: {
        AddressSelector,
        FormInput,
        TimeSelector,
        BackBtn,
    },
    methods: {
        radio2Check: function(id) {
            console.log(id);
            this.radio2Checked = id;
        },
        radioCheck: function(id) {
            console.log(id);
            this.radioChecked = id;
        },
        submit: function() {
            this.$bus.emit('page4 submit', {});
        },
    },
};
</script>

<style scoped>
.seek-part {
    background: #eeeeee;
}

.seek-type {
    width: 100%;
    /* margin-top: 1vw; */
    background: #fff;
    position: relative;
    height: 10vw;
    margin-bottom: 1px;
}
.seek-type-text {
    padding-left: 5vw;
    line-height: 10vw;
}
.seek-type-select {
    display: flex;
    flex-wrap: nowrap;
    position: absolute;
    right: 0;
    top: 0;
}
.seek-type-radio {
    height: 7vw;
    line-height: 7vw;
    min-width: 15vw;
    padding: 0 2vw;
    color: blue;
    border: 1px solid blue;
    text-align: center;
    margin: 1.5vw 2vw;
    font-size: 14px;
}
.seek-type-radio2 {
    height: 7vw;
    line-height: 7vw;
    min-width: 15vw;
    padding: 0 2vw;
    color: #5e5959;
    background: #f5f5f5;
    text-align: center;
    margin: 1.5vw 2vw;
    font-size: 9px;
}
.seek-type-radio-active {
    color: #fff;
    background: blue;
}
.seek-type-radio2-active {
    color: #42b7ea;
}

.seek-textarea {
    width: 100%;
    height: calc(100vh - 52vw);
    background: #fff;
}
.seek-textarea-title {
    padding-left: 5vw;
    padding-top: 2vw;
}
.seek-textarea-content {
    width: 100%;
    height: calc(100% - 4vw);
    border: 0;
    padding-top: 2vw;
}
</style>


