<template>
  <div class="radio-part" @click="checkedItem">
    {{formdata}}
    <div class="left-icon" :class="''">
      <div :class="{'checked':formdata.checked}"></div>
    </div>
    <div class="right-text">
      <p
        :class="formdata.checked?formdata.activeClass:formdata.fontClass"
        v-for="item in formdata.text"
        :key="item.name"
      >{{item}}</p>
    </div>
  </div>
</template>

<script>
export default {
    name: 'RadioParten1',
    methods: {
        checkedItem: function() {
            this.$bus.emit('radio click', {
                id: this.formdata.id,
                name: this.formdata.name,
            });
            console.log(this.formdata);
        },
    },
    props: ['formdata'],
};
</script>
<style scoped>
.radio-part {
    width: 100%;
    padding-top: 2.5vw;
    background: #fff;
    padding-bottom: 4vw;
}
.left-icon {
    width: 5vw;
    height: 5vw;
    position: absolute;
    left: 5vw;
    border-radius: 50%;
    background: #000;
}
.left-icon:before {
    content: '';
    width: 4vw;
    height: 4vw;
    position: absolute;
    left: 0.5vw;
    top: 0.5vw;
    border-radius: 50%;
    background: #fff;
}
.checked {
    content: '';
    width: 3vw;
    height: 3vw;
    position: absolute;
    left: 1vw;
    top: 1vw;
    border-radius: 50%;
    background: #000;
}
.checked {
}
.right-text {
    left: 15vw;
    margin-left: 15vw;
    width: 80vw;
}
</style>


