<template>
  <div class="seperator">
    <div class="seperator-title">
      <span class="seperator-title1">{{formdata.title1}}</span>
      <span class="seperator-title2">{{formdata.title2}}</span>
    </div>
    <div class="seperator-content">
      <div
        class="seperator-item"
        @click="clickItem(item)"
        v-for="item in formdata.list"
        :key="item.id"
        :class="{'seperator-checked':item.checked} "
      >{{item.name}}</div>
    </div>
  </div>
</template>
<script>
export default {
    name: 'CheckboxParten1',
    data: function() {
        return {};
    },
    computed: {},
    methods: {
        clickItem: function(item) {
            this.$bus.emit('checkbox click', {
                item: this.formdata,
                id: item.id,
            });
        },
    },
    props: ['formdata'],
};
</script>
<style>
.seperator {
    width: 100%;
    background: #fff;
}
.seperator-title1 {
    font-weight: 400;
    font-size: 14px;
    color: rgb(16, 16, 16);
    font-style: normal;
    letter-spacing: 0px;
    line-height: 16px;
    text-decoration: none;
    padding-left: 5vw;
}

.seperator-title2 {
    font-weight: 400;
    font-size: 14px;
    color: rgb(176, 173, 174);
    font-style: normal;
    letter-spacing: 0px;
    line-height: 16px;
    padding-left: 5vw;
}
.seperator-content {
    display: flex;
    flex-wrap: wrap;
}
.seperator-item {
    background-color: rgb(234, 235, 237);
    border-radius: 10px;
    font-size: 12px;
    padding: 0px;
    border-width: 0px;
    border-style: solid;
    text-align: center;
    line-height: 17px;
    font-weight: normal;
    font-style: normal;
    opacity: 1;
    padding: 1vw 3vw;
    margin: 2vw 2vw;
}
.seperator-checked {
    background: #000;
    color: #fff;
}
</style>


