<template>
  <div class="selected-part">
    <div
      v-for="item in page1BuyInfo"
      v-bind:key="item.id"
      @click="selectPerson(item.id)"
      class="selected-part-box"
    >{{item.name}}</div>
  </div>
</template>
<script>
export default {
    name: 'Page1',
    data: function() {
        return {
            page1BuyInfo: [
                { id: 0, name: '房东方' },
                { id: 1, name: '租客方' },
            ],
        };
    },
    methods: {
        selectPerson: function(id) {
            console.log('selectPerson');
            this.$bus.emit('page1 select', { id: id });
        },
    },
};
</script>

