<template>
  <div class="text-part page">
    <RadioParten1 :radiodata="radioList[0]"></RadioParten1>
    <RadioParten1 :radiodata="radioList[1]"></RadioParten1>
  </div>
</template>

<script>
import RadioParten1 from '../components/RadioParten1';
export default {
    name: 'textBlock',
    data: function() {
        return {
            radioList: [
                {
                    name: 'agree promise',
                    checked: false,
                    text: ['我承认不收客户中介费'],
                    fontClass: 'content2',
                    activeClass: 'content3',
                },
                {
                    name: 'deposit',
                    checked: false,
                    text: [
                        '保证金',
                        '支付保证金的房源，必须房源真实，如举报不真实，则不退保证金，反之签约成功',
                        '保证金可申请退回',
                        '如发布虚假房源则可能永久封号',
                    ],
                    fontClass: 'content3 ',
                    activeClass: 'content3',
                },
            ],
        };
    },
    components: {
        RadioParten1,
    },
    created: function() {
        this.$bus.on('radio click', this.radioChange);
    },
    methods: {
        radioChange: function(data) {
            console.log(data);

            this.radioList.forEach(item => {
                if (item.name == data.name) {
                    console.log(item.name);
                    item.checked = !item.checked;
                }
            });
            console.log(this.radioList);
            // this.radioData.checked = !this.radioData.checked;
        },
    },
};
</script>

<style>
.text-part {
    background: #fff;
    z-index: 10;
}
</style>

